/**
* @description This component is the root component of the application. It also
* provides some helper function which will affect all of the children components
* e.g : @method startIdleTimer
*
* @method constructor
* @method componentDidUpdate
* @method componentDidMount
* @method componentWillUnmount
* @method render
* @method checkUserLogInStatus
* @method updateWindowDimensions
* @method startIdleTimer
* @method resetIdleTimer
* @method logout
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React from 'react';
import { connect } from 'react-redux';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import cookie from 'react-cookie';
import { hashHistory } from 'react-router';

import { updateApp, logout } from 'redux/actions/AppActions';
import Loading from './loading/Loading';
import { updateUser } from 'redux/actions/UserActions';
import { loginByCookie } from 'api/Board';

class Main extends React.Component {
  constructor(props) {
    super(props);

    this.DEFAULT_IDLE_TIME = 600000; // 10 minutes

    this.idleTimer = null;

    // Component default states
    this.DEFAULT_STATES = {
      idleTime: this.DEFAULT_IDLE_TIME, // The amount of time to log out user if he/she stay idle
      isUserLoggedIn: false // To track if user is logged in or not
    };

    // To hold default windows listeners
    this.DEFAULT_LISTENERS = [
      {
        name: 'resize',
        callback: this.updateWindowDimensions
      },
      {
        name: 'mousemove',
        callback: this.resetIdleTimer
      },
      {
        name: 'onMouseDown',
        callback: this.resetIdleTimer
      },
      {
        name: 'onScroll',
        callback: this.resetIdleTimer
      },
      {
        name: 'onWheel',
        callback: this.resetIdleTimer
      }
    ];

    this.state = this.DEFAULT_STATES;

    // Bind all of the listeners
    this.DEFAULT_LISTENERS.forEach(event => {
      event.callback = event.callback.bind(this);
    }, this);

    this.startIdleTimer();
  }

  componentWillReceiveProps(nextProps, nextState)
  {
    /*
      If user has changed idleTime on app settings
      then update it on the state and reset time based on new idleTime
    */
    if (nextProps.user.settings && nextProps.user.settings.idleTime !== this.state.idleTime)
    {
      this.setState({idleTime: nextProps.user.settings.idleTime},
      () =>
      {
        clearTimeout(this.idleTimer);
        this.startIdleTimer();
      });
    }
  }

  componentDidMount() {
    this.updateWindowDimensions();
    // Add all the listeners to the window
    this.DEFAULT_LISTENERS.forEach(event => {
      window.addEventListener(event.name, event.callback);
    }, this);

    this.checkUserLogInStatus();
  }

  componentWillUnmount() {
    // Remove all the listeners from the window
    this.DEFAULT_LISTENERS.forEach(event => {
      window.removeEventListener(event.name, event.callback);
    }, this);
  }

  render() {
    if (this.state.isUserLoggedIn) {
      return (
        <div>
          <MuiThemeProvider>
            <div>
              <div>
                {this.props.children}
              </div>
            </div>
          </MuiThemeProvider>
        </div>
      );
    } else {
      return <Loading style={{ marginTop: '15%' }} />;
    }
  }

  /**
  * @description : Checks for if user is logged in or has a cookie and if so
  * let the user log in
  *
  * @author Armin Eslami
  */
  checkUserLogInStatus() {
    // First check store to see if user is logged in
    if (this.props.user && this.props.user.isLoggedIn) {
      this.setState({ isUserLoggedIn: true });
    } else if (cookie.load('_wr')) {
      // If user is not logged in in store, then check if a cookie exists
      // Get cookie token from browser
      var token = cookie.load('_wr');
      // Call proper api and wait for result
      loginByCookie(token).then(
        res => {
          process.env.NODE_ENV === 'development'
            ? console.log('loginByCookie() ended with result', res)
            : void 0;

          if (res.code === 200 && res.data && res.data.user && res.data.token) {
            // Get user data from returned result
            var uData = res.data.user;
            // Get token form returned result
            var token = res.data.token ? res.data.token : null;
            // Updated user in the store
            this.props.dispatch(
              updateUser({
                token,
                ...uData,
                rememberMe: true,
                isLoggedIn: true
              })
            );
            // Let the user log in automatically since loginByCookie() was successfull
            this.setState({ isUserLoggedIn: true });
          } else {
            // Since loginByCookie() has failed, remove the cookie and return user to /login
            cookie.remove('_wr');
            // TODO : Change hashHistory to browserHistory
            hashHistory.replace('login');
          }
        },
        err => {
          process.env.NODE_ENV === 'development'
            ? console.log('loginByCookie() ended with error', err)
            : void 0;
          // Since loginByCookie() remove the cookie and return user to /login
          cookie.remove('_wr');
          // TODO : Change hashHistory to browserHistory
          hashHistory.replace('login');
        }
      );
    } else {
      process.env.NODE_ENV === 'development'
        ? console.log('User is not logged in.')
        : void 0;
    }
  }

  /**
  * @description : Updates width and height state when the screen is resizing
  *
  * @author Armin Eslami
  */
  updateWindowDimensions() {
    this.props.dispatch(updateApp({ windowWidth: window.innerWidth }));
  }

  /**
  * @description : Starts a timer and when it ends, user will be log out
  *
  * @author Armin Eslami
  */
  startIdleTimer() {
    this.idleTimer = setTimeout(this.logout, this.state.idleTime);
  }

  /**
  * @description : Resetes idle timer and starts it again
  *
  * @author Armin Eslami
  */
  resetIdleTimer = () => {
    clearTimeout(this.idleTimer);
    this.startIdleTimer();
  };

  /**
  * @description : Logs out the user
  *
  * @author Armin Eslami
  */
  logout = () => {
    process.env.NODE_ENV === 'development'
      ? console.warn('User has been idle for too long and is logged out.')
      : void 0;
    clearTimeout(this.idleTimer);
    cookie.remove('_wr', { path: '/' });
    this.props.dispatch(updateApp({ autoLogOuted: true }));
    this.props.dispatch(logout());
  };
}

export default connect(state => {
  return {
    app: state.app,
    user: state.user
  };
})(Main);



