/**
* @description : Starts login process by sending given username and password to the server
*
* @author Armin Eslami
*
* @param username (string) : user's username
* @param password (string) : user's password
* @param rememberMe (bool) : True if users asks for being remembered
*
* @return server response as an object
*/
export function login(username, password, rememberMe) {
  if (!username || !password) {
    return new Promise((resolve, reject) => {
      reject(INVALID_DATA);
    });
  }

  return axios.get(requestUrl).then(
    function(res) {
      var serverResponse = {
        status: 'success',
        code: 200,
        data: {
          token: 'LOGIN_TOKEN',
          user: {
            firstName: '',
            lastName: '',
            avatar: null,
            universities: [
              {
                uniCode: '3RB0kXYdVDLkVoFmz31f/EQHS9aTuAvBlWP9eKZ5h+1=',
                uniName: '    ',
                type: 2
              },
              {
                uniCode: '3RB0kXYdVDLkVoFmz31f/EQHS9aTuAvBlWP9eKZ5h+2=',
                uniName: '      ',
                type: 1
              },
              {
                uniCode: '3RB0kXYdVDLkVoFmz31f/EQSXS9aTuAvBlWP9eKZ5h+2=',
                uniName: '     ',
                type: 0
              }
            ],
            settings: {
              messageFetchLimit: 10,
              idleTime: 300000,
              combinedFetch: false
            }
          }
        }
      };
      return serverResponse;
    },
    function(err) {
      process.env.NODE_ENV === 'development'
        ? console.log('API > login() failed with error :', err)
        : void 0;
      return FATAL_ERROR;
    }
  );
}

