/**
* @description : Logins user by his/her university. Usually this is called when user has more than one usniveristy
*
* @author Armin Eslami
*
* @param token (string) : User's access token
* @param uniCode (string) : User's university code
* @param userType (number) : User's type on given university
* @param rememberMe (boolean) : True if users asks for being remembered
*
* @return server response as object
*/
export function selectAccount(token, uniCode, userType, rememberMe) {
  if (!token || token.length === 0 || !uniCode || userType === null) {
    return new Promise((resolve, reject) => {
      reject(INVALID_DATA);
    });
  }

  return axios.get(requestUrl).then(
    function(res) {
      var serverResponse = {
        status: 'success',
        code: 200,
        data: {
          token: 'SELECT_ACCOUNT_TOKEN'
        }
      };
      return serverResponse;
    },
    function(err) {
      process.env.NODE_ENV === 'development'
        ? console.log('API > selectAccount() failed with error :', err)
        : void 0;
      return FATAL_ERROR;
    }
  );
}

/**
* @description : Fetches messages from the server
*
* @author Armin Eslami
*
* @param token (string) : User's access token
* @param limit (number) : The maximum number of messages to be fetched
* @param offset (number) : Indicates that from what position should this function return user's messages
* @param combinedFetch (bool) : If true, it will fetch messages regardless of user's current account
*
* @return server response as object
*/
export function fetchMessages(token, limit, offset = 0, combinedFetch) {
  if (!token || token.length === 0 || !limit) {
    return new Promise((resolve, reject) => {
      reject(INVALID_DATA);
    });
  }

  //REMOVE
  var messages = [];
  var counter = 0;
  if (MESSAGES.length > 0) {
    for (var i = offset; counter < limit; i++) {
      messages.push(MESSAGES[i]);
      counter++;
    }
  }

  return axios.get(requestUrl).then(
    function(res) {
      var serverResponse = {
        status: 'success',
        code: 200,
        data: {
          token: 'FETCH_MESSAGE_TOKEN',
          messages,
          hasMore: offset === 0
        }
      };
      return serverResponse;
    },
    function(err) {
      process.env.NODE_ENV === 'development'
        ? console.log('API > fetchMessages() failed with error :', err)
        : void 0;
      return FATAL_ERROR;
    }
  );
}

